.App {
  text-align: center;
}
#Apptop {
  top: 0px;
}
.App-header {
  background-image:url("./page/content/bgi.jpg");
  background-position:center center; /*background-color: #282c34;*/
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
#App-header_text {
  font-family:Quicksand,sans-serif;
  margin:0 auto;
  position:relative;
  text-align:center;
  width:60%;
  color:rgb(222,222,222);
  font-size: 3.5vh;
  font-weight:100;
  line-height:8vh;
}
#App-header_text_title {
  color:rgb(226,226,226);
  font-size:8vh;
  font-weight:500;
  line-height:12vh;
}
#App-header_text_time {
  /*font-family:SimHei,sans-serif;*/
  font-size:3vh;
  line-height:6vh;
}
.App-iconR {
  top: 7vh;
  height: 18vh;
  width: 18vh;
  /*pointer-events: none;*/
  display:block;
  cursor:pointer;
  border-radius:90%;
  margin:0 auto;
  position: relative;
  opacity:0.8;
  -webkit-transition:all 0.5s;
  -o-transition:all 0.5s;
  -moz-transition:all 0.5s;
  transition:all 0.5s;
  z-index:2;
}
.App-iconR:hover {
  opacity:0.45;
}
.App-iconR-img {
  height: 18vmin;
  width: 18vmin;
}
.App-link {
  color: #61dafb;
}
nav {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 10px;
}
.background {
  position: absolute;
  margin:0 auto;
  text-align:center;
  top: 0;
  left: 0;
  bottom: 0;
  width: 53vmin;
  background: rgba(33,33,33,0.7);
} 
button {
  outline: none;
  border: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  cursor: pointer;
  position: absolute;
  margin:0 auto;
  top: 14px;
  left: 11px;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: transparent;
} 
ul,
li {
  margin: 0;
  padding: 0;
}
ul {
  position: absolute;
  top: 18vh;
  left: 5vmin;
}
li {
  list-style: none;
  margin-bottom: 6vmin;
  display: flex;
  align-items: center;
  cursor: pointer;
}
li a {
  font-size: 6vmin;
  color: rgb(225,225,225);
}
.icon-placeholder {
  width: 9vmin;
  height: 9vmin;
  border-radius: 50%;
}
.text-placeholder {
  font-size: 6vmin;
  margin-left: 2vmin;
  border-radius: 2vmin;
  width: 28vmin;
  height: 9vmin;
  flex: 1;
}
.refresh {
  padding: 10px;
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
  width: 20px;
  height: 20px;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
